import React, { Component } from 'react';
//import logo from './logo.svg';
//import './App.css';
import base from '../base';
import Tweet from './Tweet';

import sampleTweets from '../sample-tweets';

class App extends Component {
  constructor() {
    super();

  this.updateFavorites = this.updateFavorites.bind(this);
  this.loadTweets = this.loadTweets.bind(this);
    //getinitialState
    this.state = {
      tweets: {},
      favorites: {},
      text: ''
    };
  }

  componentWillMount(){
    //this runs right before the app is rendered
    this.ref = base.syncState(`hackmods`,
    {
      context: this,
      state: 'tweets'
    });

       if(this.state.favorites === null)
      {
        this.state.favorites = this.state.tweets;
      }
  }

  componentWillUnmount(){
    base.removeBindings(this.ref);
  }

  loadTweets() {
    this.setState({
      tweets: sampleTweets
    });
  }

  updateFavorites(event){
    this.setState({text: event.target.value});
   let favoriteQueue = {};
   //alert(text); 
   //mkellly_
   const regex = new RegExp("" + this.state.text + "", "i");

   const regexpattern = /${this.state.text}/i; // /(love|hate) trump/i;

    this.state.tweets.map(function(tweet, i){
    const resultA = regex.test(tweet.text);
    const resultB = regex.test(tweet.user.name);
    const resultC = regex.test(tweet.user.screen_name);
      if(resultA | resultB | resultC)
      {
        favoriteQueue[`tweet-${i}`] = tweet;
      } 
     });

    if(Object.keys(favoriteQueue).length === 0)
    {
      favoriteQueue = this.state.tweets;
    }

    this.setState({
      favorites: favoriteQueue
    });
  
   // this.setState( {favorites});
   //<input type="text"  onChange={(text) => this.setState({text})} value={this.state.text} />
}

  render() {
    return (
      <div className="App">
      <div className="search-deck">
          <div className="input-group">
           <input type="text" onChange={(text) =>  this.updateFavorites(text)}  className="form-control" placeholder="Search my favorite tweets!"  />
          </div>
        <div className="card-deck">
            <div className="card-deck">
             { Object
                .keys(this.state.favorites)
                .map(key => <Tweet key={key} index={key} details={this.state.favorites[key]}/>)
              }
              </div>
          </div>
          </div>
      </div>
    );
  }
}

export default App;

