import React from 'react';
import {parseDate} from '../helpers';

//import linkify from 'linkifyjs';
//require('linkifyjs/plugins/hashtag')(linkify); // optional 
//import linkifyHtml from 'linkifyjs/html';

class Tweet extends React.Component {
	render(){
		const details = this.props.details;
		//const {details, index}= this.props;
		//const isAvailable= details.status === 'available';
		//const buttonText= isAvailable ? 'Add To Order' : 'Sold Out!';
		//				<img className="card-img-top" src={details.user.profile_background_image_url} alt={details.user.screen_name} />

		//const fixed = linkifyHtml(details.text, {defaultProtocol: 'http'});
		return (
			<div className="card">
            	<div className="card-block">
            		<h4 className="card-title">@{details.user.screen_name}</h4>
            		<p className="card-text">{details.text}</p>
            	</div>
            	<div className="card-footer">
           			<small className="text-muted">{parseDate(details.created_at)}</small>
            	</div>
            </div>
		)
	}
}


export default Tweet;


